@import "src/common/styles/sass/variables";
@import "src/common/styles/sass/mixins";


.buttonWrapper {
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 5px;
  right: 5px;

  @media (min-width: 625px) {
    display: none;

  }

  .button {
    position: relative;
    width: 50px;
    height: 5px;
    background-color: $accentColor;

    &::before {
      position: absolute;
      left: 0;
      top: -10px;
      content: '';
      display: block;
      width: 50px;
      height: 5px;
      background-color: $accentColor;
      transition: transform 0.2s ease-in 0.2s, top 0.2s linear;

    }

    &::after {
      position: absolute;
      left: 0;
      top: 10px;
      content: '';
      display: block;
      width: 50px;
      height: 5px;
      background-color: $accentColor;
      transition: transform 0.2s ease-in 0.2s, top 0.2s linear;


    }

  }

  .buttonActive {
    background-color: transparent;

    &::before {
      transform: rotate(45deg);
      top: 0;
      transition: top 0.2s linear, transform 0.2s ease-in 0.2s;

    }

    &::after {
      transform: rotate(-45deg);
      top: 0;
      transition: top 0.2s linear, transform 0.2s ease-in 0.2s;

    }
  }

}








